import React,{useCallback, useEffect,useState} from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import {
  Container,
  Divider,
  Grid,
  Tab,
  Tabs,
} from "@material-ui/core";
import { Box, Breadcrumbs, Button, Link } from "@material-ui/core";
import UploadIcon from "../icons/Upload";
import DownloadIcon from "../icons/Download";

import { useLocation } from "react-router-dom";
import ChevronRightIcon from "../icons/ChevronRight";
import { Link as RouterLink } from "react-router-dom";
import StudentAttendence_Tab from "./StudentAttendence_Tab";
import { CustomerContactDetails, CustomerDataManagement, CustomerEmailsSummary, CustomerInvoicesSummary } from "../components/dashboard/customer";
import useSettings from "../hooks/useSettings";
import FatherContactDetail from "../components/dashboard/customer/FatherContactDetail";
import {customAxios} from "./apis/Axios";
import useMounted from "../hooks/useMounted";
import ExamsReport from "./ExamsReport";

const tabs = [
  { label: "Profile", value: "profile" },
  { label: "Exam", value: "exam" },
  { label: "Attendence", value: "attendence" },

];
const Student_Detail = () => {
  const mounted = useMounted();

  const { state } = useLocation();
  const {settings}=useSettings();
  const { row } = state;
  const [currentTab, setCurrentTab] = useState("profile");
 const [report ,setReport]=useState([])
  const changeTab = (event, value) => {
    setCurrentTab(value);
  };
 
const feeReport=useCallback(async()=>{
  try{
    const Report = await customAxios.getCall(
      `list/studentTransection/${row.rollno}`
    );
    if (mounted.current) {
        console.log(Report)
        setReport(Report);
      }

  }catch(err){
    console.log(err)
  }
},[mounted])

useEffect(()=>{
feeReport();
},[feeReport])

  return (
    <>
      <Container maxWidth={settings.compact ? "xl" : false}>
        <Grid
          style={{ paddingTop: "30px", paddingLeft: "30px" }}
          container
          justifyContent="space-between"
          spacing={3}
        >
          <Grid item>
            <Typography color="textPrimary" variant="h5">
              Student Detail
            </Typography>
            <Breadcrumbs
              aria-label="breadcrumb"
              separator={<ChevronRightIcon fontSize="small" />}
              sx={{ mt: 1 }}
            >
              <Link
                color="textPrimary"
                component={RouterLink}
                to="/dashboard/maindash"
                variant="subtitle2"
              >
                Dashboard
              </Link>
              <Link
                color="textPrimary"
                component={RouterLink}
                to="/dashboard/all-students"
                variant="subtitle2"
              >
                Student List
              </Link>
              <Typography color="textSecondary" variant="subtitle2">
                Student Detail
              </Typography>
            </Breadcrumbs>
            <Box
              sx={{
                mb: -1,
                mx: -1,
                mt: 1,
              }}
            >
              <Button
                color="primary"
                startIcon={<UploadIcon fontSize="small" />}
                sx={{ m: 1 }}
              >
                Import
              </Button>
              <Button
                color="primary"
                startIcon={<DownloadIcon fontSize="small" />}
                sx={{ m: 1 }}
              >
                Export
              </Button>
            </Box>
          </Grid>
        </Grid>
        <Box sx={{ mt: 3, mb: 1, marginLeft: "35px" }}>
          <Tabs
            indicatorColor="primary"
            onChange={changeTab}
            scrollButtons="auto"
            textColor="primary"
            value={currentTab}
            variant="scrollable"
          >
            {tabs.map((tab) => (
              <Tab key={tab.value} label={tab.label} value={tab.value} />
            ))}
          </Tabs>
        </Box>
        <Divider />
        <Box>
          {currentTab === "profile" && (
            <Grid container spacing={3}>
              <Grid
                item
                lg={settings.compact ? 6 : 4}
                md={6}
                xl={settings.compact ? 6 : 3}
                xs={12}
              >
                <CustomerContactDetails
                  row={row}
                  address1={row.address1}
                  address2={row.address2}
                  email={row.email}
                  isVerified={row.status}
                  phone={row.phone}
                />
              </Grid>
              <Grid
                item
                lg={settings.compact ? 6 : 4}
                md={6}
                xl={settings.compact ? 6 : 3}
                xs={12}
              >
                <FatherContactDetail
                  row={row}
                  address1={row.address1}
                  address2={row.address2}
                  email={row.email}
                  isVerified={row.status}
                  phone={row.phone}
                />
              </Grid>
              <Grid
                item
                lg={settings.compact ? 6 : 4}
                md={6}
                xl={settings.compact ? 6 : 3}
                xs={12}
              >
                <CustomerEmailsSummary row={row} />
              </Grid>
              <Grid
                item
                lg={settings.compact ? 6 : 4}
                md={6}
                xl={settings.compact ? 6 : 3}
                xs={12}
              >
                <CustomerInvoicesSummary report={report} row={row} />
              </Grid>
            </Grid>
          )}

          {currentTab === "exam" && <ExamsReport ID={row.id} />}
          {currentTab === "attendence" && (
            <StudentAttendence_Tab
              row={row.admissiondate}
              rollno={row.rollno}
              name={`${row.name} `}
            />
          )}
        </Box>
      </Container>
    </>
  );
};

export default Student_Detail;
