import React, { useEffect, useState, useCallback } from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import {
  Box,
  Card,
  CircularProgress,
  FormControl,
  InputLabel,
  MenuItem,
  Typography,
} from "@material-ui/core";
import { Select } from "@material-ui/core";
import { customAxios } from "./apis/Axios";
import useMounted from "../hooks/useMounted";
import wait from "../utils/wait";

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}

const rows = [
  createData("Frozen yoghurt", 159, 6.0, 24, 4.0),
  createData("Ice cream sandwich", 237, 9.0, 37, 4.3),
  createData("Eclair", 262, 16.0, 24, 6.0),
  createData("Cupcake", 305, 3.7, 67, 4.3),
  createData("Gingerbread", 356, 16.0, 49, 3.9),
];

const useStyles = makeStyles({
  table: {
    minWidth: 700,
  },
});

export default function ExamsReport({ ID }) {
  // console.log(ID)
  const mounted = useMounted();
  const classes = useStyles();
  const [examgroup, setExamgroup] = useState([]);
  const [examsDatas, setexamsData] = useState([]);
  const [selectedValue, setSelectedValue] = useState();
  const [loading, setLoading] = useState(false);
  var handleChange = async (e) => {
    // console.log(e.target.value)
    const selectedId = e.target.value;
    setSelectedValue(selectedId);

    try {
      setLoading(true);
      await wait(2000);
      const examsData = await customAxios.getCall(
        `exams/exams-deatils/${ID}/${selectedId}`
      );
      setexamsData(examsData);
      setLoading(false);
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    console.log("djhfakjds", selectedValue, examsDatas, examgroup);
  }, [selectedValue]);

  useEffect(async () => {
    console.log("2");

    const examsGroupData = await customAxios.getCall("exams/examsName");
    setExamgroup(examsGroupData);
  }, []);

  //   const examsReport = useCallback(async () => {
  //     try{

  //         const examsData = await customAxios.getCall(`exams/exams-deatils/${ID}/${selectedValue}`);
  //         if (mounted.current) {
  //             setexamsData(examsData);
  //         }
  //     }catch(err){
  //         console.log(err)
  //     }
  //   }, [mounted]);

  //   useEffect(() => {
  //     console.log("3");
  //     examsReport();
  //   }, [selectedValue]);

  function ccyFormat(value) {
    console.log(value)
    return `${value.toFixed(2)}`;
  }

  var sum1 = 0;
  var sum2 = 0;
  return (
    <>
      <Card sx={{ mt: 2 }}>
        <FormControl>
          <InputLabel id="examsName">Exams Name</InputLabel>

          <Select
            style={{ width: 227 }}
            // fullWidth
            labelId="examsName"
            value={selectedValue}
            onChange={handleChange}
            id="examsName"
            label="examsName"
            name="examsName"
          >
            {examgroup.map((option) => (
              <MenuItem key={option.id} value={option.id}>
                {option.name}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Card>
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Name</StyledTableCell>
              <StyledTableCell align="right">Marks Obtain</StyledTableCell>
              <StyledTableCell align="right">Max Marks</StyledTableCell>
              <StyledTableCell align="right">Percentage</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {loading ? (
              <TableRow>
                <TableCell colSpan={4}>
                  <Box
                    sx={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    <CircularProgress />
                  </Box>
                </TableCell>
              </TableRow>
            ) : examsDatas.length ? (
              examsDatas.map((row) => {
                sum1 = sum1 + row.MaxMarks;
                sum2 = sum2 + row.ObtMarks;
                return (
                  <>
                    <StyledTableRow key={row.id}>
                      <StyledTableCell component="th" scope="row">
                        {row.subjectName}
                      </StyledTableCell>
                      <StyledTableCell align="right">
                        {row.MaxMarks}
                      </StyledTableCell>
                      <StyledTableCell align="right">
                        {row.ObtMarks}
                      </StyledTableCell>
                      <StyledTableCell align="right">
                        {row.ObtMarks}
                      </StyledTableCell>
                    </StyledTableRow>
                  </>
                );
              })
            ) : (
              <TableRow>
                <TableCell colSpan={4}>
                  <div style={{ display: "flex", justifyContent: "center" }}>
                    <Typography variant="h6">Exams Data Not Found</Typography>
                  </div>
                </TableCell>
              </TableRow>
            )}
            <TableRow>
              <TableCell>Total</TableCell>
              <TableCell align="start">{sum1}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell>Obtain</TableCell>
              <TableCell align="start">{sum2}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell>Percentage</TableCell>
              <TableCell align="start">
                {sum1&&sum2 != 0 ? ccyFormat((sum2 / sum1) * 100):0}
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}
